.App {
  background-color: yellowgreen;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.prev-result,
.curr-number {
  width: 27.8%;
  height: 50px;

  text-align: right;
  padding-right: 20px;
  font-weight: 900;
}

.oparends {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.curr-number,
.oparends {
  border-bottom: 5px solid black;
}

.curr-number {
  font-size: 35px;
  padding-bottom: 15px;
}

.prev-result {
  border-top: 5px solid black;
  padding-top: 15px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  font-size: 20px;
}

.prev-result,
.curr-number,
.oparends {
  border-left: 5px solid black;
  border-right: 5px solid black;
}

.oparends div {
  display: flex;
  justify-content: space-between;
}

button {
  background: none;
  height: 80px;
  width: 80px;
  font-size: 20px;
  margin: 10px;
  border-radius: 10px;
  background-color: yellow;
  font-weight: 600;
  cursor: pointer;
}
